import { useTranslation } from "react-i18next";
import { useLocation } from "react-router-dom";
import { useGuideMeetinDetail } from "../../../Hooks/ApiHooks/GuideManager/guide-manager-api";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import FormInputView from "../../../Ui/Input/FormInputView";
import dayjs from "dayjs";
import { useState } from "react";
import RectangleTabWrapper from "../../../Ui/Tab/Rectangle/RectangleTabWrapper";
import RectangleTabCard from "../../../Ui/Tab/Rectangle/RectangleTabCard";
import GuideMeetingDetailCard from "./GuideMeetingDetailCard";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import QvipleLoader from "../../../Loader/QvipleLoader";

const GuideMeetingDetail = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [open, setOpen] = useState("");

  const { guideMeetinDetail, guideMeetinDetailLoading } = useGuideMeetinDetail({
    data: {
      meid: getQuery.state?.meeting_detail?._id,
    },
    skip: !getQuery.state?.meeting_detail?._id,
  });
  const onTabChange = (val) => {
    if (val === open) {
      setOpen("");
    } else {
      setOpen(val);
    }
  };

  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("meeting_detail")}</h6>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        <FormInputView
          label={`${t("agenda")}: `}
          value={guideMeetinDetail?.agenda ?? ""}
        />
        <FormInputView
          label={`${t("summary")}: `}
          value={guideMeetinDetail?.summary ?? ""}
          customFormInputContainer={{
            alignItems: "flex-start",
          }}
        />
        <FormInputView
          label={`${t("discussion")}: `}
          value={guideMeetinDetail?.discussion ?? ""}
          customFormInputContainer={{
            alignItems: "flex-start",
          }}
        />
        <FormInputView
          label={`${t("date")}: `}
          value={dayjs(guideMeetinDetail?.created_at).format("DD/MM/YYYY")}
        />

        <RectangleTabWrapper>
          {tabList.map((tab) => (
            <RectangleTabCard
              key={tab?.id}
              label={tab.label}
              onClick={() => onTabChange(tab?.id)}
              isActive={open === tab?.id ? true : false}
            />
          ))}
        </RectangleTabWrapper>

        {open === "present" ? (
          <>
            {guideMeetinDetail?.present_mentees?.map((student) => (
              <GuideMeetingDetailCard
                student={student}
                isPresent
                key={student?._id}
              />
            ))}
            {guideMeetinDetail?.present_mentees?.length === 0 && (
              <EmptyMessage
                title="No item."
                customStyleContainer={{
                  marginTop: "2rem",
                }}
              />
            )}
          </>
        ) : open === "absent" ? (
          <>
            {guideMeetinDetail?.absent_mentees?.map((student) => (
              <GuideMeetingDetailCard student={student} key={student?._id} />
            ))}
            {guideMeetinDetail?.absent_mentees?.length === 0 && (
              <EmptyMessage
                title="No item."
                customStyleContainer={{
                  marginTop: "2rem",
                }}
              />
            )}
          </>
        ) : null}
      </UserMemberContentWrapper>
      {guideMeetinDetailLoading && <QvipleLoader />}
    </>
  );
};

export default GuideMeetingDetail;

const tabList = [
  {
    label: "present",
    id: "present",
  },
  {
    label: "absent",
    id: "absent",
  },
];
